/* eslint @okta/okta-ui/no-specific-modules: 0 */
import Handlebars from 'handlebars';
import _ from '../underscore-wrapper';

const CACHE_BUST_URL_PREFIX = '/assets';

function prependCachebustPrefix(path) {
  if (path.indexOf(CACHE_BUST_URL_PREFIX) === 0) {
    return path;
  }
  return CACHE_BUST_URL_PREFIX + path;
}

Handlebars.registerHelper('img', function img(options) {
  const cdn = (typeof okta !== 'undefined' && okta.cdnUrlHostname) || '';

  const hash = _.pick(options.hash, ['src', 'alt', 'width', 'height', 'class', 'title']);

  hash.src = `${cdn}${prependCachebustPrefix(hash.src)}`;

  const attrs = _.map(hash, (value, attr) => {
    const encodedValue = attr === 'src' ? encodeURI(value) : Handlebars.Utils.escapeExpression(value);
    return `${attr}="${encodedValue}"`;
  });

  return new Handlebars.SafeString(`<img ${attrs.join(' ')}/>`);
});

export default Handlebars;
